import { useState, useEffect } from 'react'
import useStats from "../src/useStats";

export default function CountrySelector() {
    const stats = useStats('https://covid19.mathdro.id/api/countries');
    if(!stats) return <p>Loading...</p>
    console.log(stats);
    return (
        <div>
            <p>Countries</p>
        </div>
    );
}